/**
 * Client VaultBlock interface
 */
export interface VaultBlock extends VaultBlockData, ClientEntity {}

export function sVaultBlock(isPartial?: boolean) {
  return S.object()
    .id('#vaultBlock')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sVaultBlockData(isPartial))
}

export interface VaultBlockData extends VaultBlockBody {
  vaultId: string
  vaultAddress: string
  block: Block
}

export interface VaultBlockInterestRates {
  // Interest rate from base strategy
  BASE: number
  // Interest rate from harvested rewards
  HARVEST?: number
  // Interest rate from distributed rewards
  REWARDS?: number
  // Interest rate including fees
  GROSS?: number
  // Net Interest rate (GROSS - fee)
  NET?: number
  // Fees in percentage
  FEE?: number
}

export interface VaultBlockAPRs extends VaultBlockInterestRates {}
export interface VaultBlockAPYs extends VaultBlockInterestRates {}

export interface VaultBlockEpochPrices {
  NEGATIVE: string
  START: string
  CURRENT: string
  MIDDLE: string
  END: string
}

export function sVaultBlockInterestRates() {
  return S.object()
    .additionalProperties(false)
    .prop('BASE', S.number())
    .required()
    .description('Interest rate from base strategy')
    .prop('HARVEST', S.number())
    .description('Interest rate from harvested rewards')
    .prop('REWARDS', S.number())
    .description('Interest rate from distributed rewards')
    .prop('GROSS', S.number())
    .description('Interest rate including fees')
    .prop('NET', S.number())
    .description('Interest rate net of fees')
    .prop('FEE', S.number())
    .description('Fees in percentage')
}

export interface VaultBlockInterest {
  // Interest amount including fees
  GROSS: iBigInt
  // Net Interest amount (GROSS - fee)
  NET: iBigInt
  // Fees amount
  FEE: iBigInt
}

export function sVaultBlockInterest() {
  return S.object()
    .additionalProperties(false)
    .prop('GROSS', sBigInt())
    .description('Interest amount including fees')
    .prop('NET', sBigInt())
    .description('Net Interest amount (GROSS - fee)')
    .prop('FEE', sBigInt())
    .description('Fees amount')
}

export interface RewardToken {
  _id: string
  address: string
}

export interface VaultBlockRewardProgram {
  tokenId: string
  APR: number
  USD?: iBigInt
}

export function sVaultBlockRewardProgram() {
  return S.object()
    .additionalProperties(false)
    .prop('tokenId', sStringId())
    .required()
    .prop('APR', S.number())
    .required()
    .prop('USD', sBigInt())
}

export interface VaultBlockBody {
  // Common data
  APRs: VaultBlockAPRs
  APYs: VaultBlockAPYs
  interest?: VaultBlockInterest
  lastInterest?: VaultBlockInterest
  totalSupply: iBigInt
  price: iBigInt
  TVL?: VaultBlockTvl
  pools?: VaultBlockPool[]
  totalRequests?: VaultBlockTotalRequests
  rewardPrograms?: VaultBlockRewardProgram[]

  // ContractType based data
  cdo?: VaultBlockCdo
  cdoEpoch?: VaultBlockCdoEpoch
  strategy?: VaultBlockStrategy
  paretoDollar?: VaultBlockParetoDollar
  allocations?: VaultBlockAllocation[]
  requests?: VaultBlockRequest[]
}

export function sVaultBlockBody(isPartial?: boolean) {
  return (
    S.object()
      .additionalProperties(false)

      // Common data
      .prop('APRs', sVaultBlockInterestRates())
      .description('Non-coumponded interest rates')
      .prop('APYs', sVaultBlockInterestRates())
      .description('Compounded interest rates')
      .prop('interest', sVaultBlockInterest())
      .prop('lastInterest', sVaultBlockInterest())
      .description('Interest amounts')
      .prop('totalSupply', sBigInt())
      .prop('price', sBigInt())
      .prop('TVL', sVaultBlockTvl())
      .prop('pools', S.array().items(sVaultBlockPool()))
      .prop('totalRequests', sVaultBlockTotalRequests())
      .prop('rewardPrograms', S.array().items(sVaultBlockRewardProgram()))

      // ContractType based data
      .prop('cdo', sVaultBlockCdo())
      .prop('cdoEpoch', sVaultBlockCdoEpoch())
      .prop('strategy', sVaultBlockStrategy())
      .prop('paretoDollar', sVaultBlockParetoDollar())
      .prop('allocations', S.array().items(sVaultBlockAllocation()))
      .prop('requests', S.array().items(sVaultBlockRequest()))

      .required(isPartial ? [] : ['APRs', 'APYs', 'totalSupply', 'price'])
  )
}

