export interface VaultBlockCdoEpoch {
  apr: number
  lastApr?: number
  lastInterest: iBigInt

  /**
   * WARNING: Do not use this value anymore
   * mode STRATEGY -> NET INTEREST
   * mode CREDIT -> GROSS INTEREST
   * @deprecated
   */
  expectedInterest: iBigInt

  deposits: iBigInt
  duration: number
  bufferDuration?: number
  unclaimedFees: iBigInt
  contractValue?: iBigInt

  epochNumber?: number
  startDate?: string
  endDate?: string
  startCureDate?: string
  count?: number

  status: VautlBlockEpochStatus
  withdrawType?: VaultEpochWithdrawType

  depositQueue?: VaultBlockCdoEpochQueue
  withdrawQueue?: VaultBlockCdoEpochQueue

  instantWithdraws?: VaultBlockCdoEpochInstantWithdraws
  withdraws?: VaultBlockCdoEpochWithdraws
}

export function sVaultBlockCdoEpoch() {
  return S.object()
    .additionalProperties(false)
    .prop('apr', S.number())
    .required()
    .prop('lastInterest', sBigInt())
    .required()
    .prop('expectedInterest', sBigInt())
    .required()
    .prop('unclaimedFees', sBigInt())
    .required()
    .prop('deposits', sBigInt())
    .required()
    .prop('duration', S.number())
    .required()
    .prop('status', sVautlBlockEpochStatus())
    .required()
    .prop('contractValue', sBigInt())
    .prop('lastApr', S.number())
    .prop('count', S.number())
    .prop('startDate', sDateString())
    .prop('endDate', sDateString())
    .prop('startCureDate', sDateString())
    .prop('epochNumber', S.number())
    .prop('bufferDuration', S.number())
    .prop('withdrawType', sVaultEpochWithdrawType())
    .prop('withdraws', sVaultBlockCdoEpochWithdraws())
    .prop('depositQueue', sVaultBlockCdoEpochQueue())
    .prop('withdrawQueue', sVaultBlockCdoEpochQueue())
    .prop('instantWithdraws', sVaultBlockCdoEpochInstantWithdraws())
}

